import * as moment from 'moment'
import Calendar from '@/components/common/Calendar.component'
export default {
  name: 'SimpleMonthCalendar',
  components: {
    Calendar
  },
  props: {
    date: {
      type: Object,
      default: function () {
        return moment()
      }
    },
    data: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  methods: {
    isOrder (data) {
      return data && data.render_type === 1
    },
    isOrderStart (data) {
      return data && [2, 4].includes(data.render_type)
    },
    isOrderEnd (data) {
      return data && [3, 4].includes(data.render_type)
    }
  }
}



