import { Realtime } from 'leancloud-realtime'
import store from '@/store'
import { Store } from '@/service/store.service'
import router from '@/router'
import { Thread, Message } from '@/store/message'

const realtime = new Realtime({
  appId: 'mqx01tj38Ml20KwYDnJUzoF4-gzGzoHsz',
  appKey: '2gzsHCroWenrOp18y4N2h6kP',
  region: 'cn'
})
function handleNewMessage (data) {
  const route = router.currentRoute
  const path = route && route.path
  if (path === '/message/list') {
    /** thread list 页面 */
    const thread = new Thread({
      ...data,
      ...{ id: data.thread_id },
      ...{ unread_count: data.thread_unread_num }
    })
    const list = store.state.message.threads
    let index = list.findIndex(thread => thread.id === data.thread_id)
    if (index !== -1) {
      /** 更新 */
      store.commit('message/updateThread', { index: index, thread: thread })
    } else {
      /** 新的thread */
      store.commit('message/unshiftThread', thread)
    }
    store.commit('UPDATE_UNREAD', data.total_unread_num)
  } else if (path === '/message/detail') {
    /** 聊天页面 */
    const threadId = route.query && route.query.thread_id
    if (threadId === data.thread_id) {
      const message = new Message(data.new_inquiry)
      store.commit('message/appendMessage', message)
    }
  } else {
    store.commit('UPDATE_UNREAD', data.total_unread_num)
  }
}

function getTotalUnreadCount () {
  store.dispatch('message/getTotalUnreadCount')
}

export default {
  listen: () => {
    listen()
  }
}

/** 连接长连接 */
function connect () {
  return new Promise((resolve, reject) => {
    let timer = setInterval(() => {
      console.log('try connect ...')
      if (Store.get('operator_id')) {
        clearInterval(timer)
        timer = null

        resolve(realtime.createIMClient('operator' + Store.get('operator_id')))
        // resolve(realtime.createIMClient('operator10004'))
      }
    }, 10000)
  })
}

/** 监听消息并自动重试 */
function listen () {
  connect()
    .then(client => {
      console.log('connect success!')
      getTotalUnreadCount()
      client.on('message', (message, conversation) => {
        console.log('get message!')
        handleMessage(message)
      })
    })
    .catch(err => {
      console.log('connect error: ', err)
      listen()
    })
}

/** 处理消息 */
function handleMessage (message) {
  console.log(message)
  const content = message.content
  switch (content.type) {
    case 'unread_inquiry_num':
      // handleUnreadMessageCount(content.body)
      break
    case 'unread_sys_num':
      // handleUnreadNotificationCount(content.body)
      break
    case 'new_inquiry':
      handleNewMessage(content.body)
      break
    case 'new_sys_info':
      // handleNewNotification(content.body)
      break
  }
}



