import { Http } from '@/service/http.service'
import * as moment from 'moment'

class Calendar {
  constructor (options) {
    this.data = options.data
    this.month = options.month
  }
}

export default {
  namespaced: true,
  state: {
    houses: [{}, {}, {}],
    calendars: [],
    overview: { /** 日历总览，点状图 */
      list: [],
      total: 0
    }

  },
  mutations: {
    increaseByPush (state, payload) {
      state.calendars.push(new Calendar({
        data: [],
        month: payload
      }))
    },
    increaseByUnshift (state, payload) {
      state.calendars.unshift(new Calendar({
        data: [],
        month: payload
      }))
    },
    setCalendarData (state, payload) {
      const { index, data } = payload
      formatCalendarList(data)
      state.calendars[index].data = data
      // Array.prototype.push.apply(state.calendars[index].data, data)
    },
    setHouses (state, payload) {
      state.houses = payload
    },
    removeCalendarsData (state) {
      state.calendars = []
    },
    setOverview (state, payload) {
      const { list, total } = payload
      list.forEach(res => formatCalendarList(res.calendar))
      state.overview.list = list
      state.overview.total = total
    },
    appendOverviewList (state, payload) {
      const { list } = payload
      list.forEach(res => formatCalendarList(res.calendar))
      state.overview.list = [...state.overview.list, ...list]
    }
  },
  actions: {
    initCalendars ({ state, commit }) {
      commit('increaseByPush', moment())
      commit('increaseByPush', moment().add(1, 'M'))
      commit('increaseByPush', moment().add(2, 'M'))
    },
    async getCalendarHouseList ({ state, commit }) {
      let res = await Http.get('/mobile_api/calendar/house_summary_list')
      if (res && res.data) {
        commit('setHouses', res.data.list)
      }
    },
    async getCalendarDataByMonths ({ state, commit }, params) {
      let res = await Http.get('/mobile_api/calendar/months_calendar', params)
      let list = res.data && res.data.list
      list && list.forEach((d, i) => {
        /** 为每一个月多添加一天，用来确认订单的开始 */
        if (i > 0) {
          const prev = list[i - 1]
          d.data.unshift(JSON.parse(JSON.stringify(prev.data[prev.data.length - 1])))
        }
        const index = state.calendars.findIndex((c) => c.month.format('YYYY-MM') === d.month)
        if (index !== -1) {
          commit('setCalendarData', {
            index: index,
            data: d.data
          })
        }
      })
    },
    /** 房源日历列表，点状图 */
    async getOverviewList ({ state, commit }, params) {
      return Http.get('/mobile_api/calendar/list', params)
    },
    /** 单个房源价格日历 */
    getPriceCalendar ({ state, commit }, params) {
      return Http.get('/mobile_api/calendar/price_calendar', params)
    },
    /** 手工预订 */
    bookManual (_, params) {
      return Http.post('/mobile_api/calendar/book_by_hand', params)
    },
    /** 锁房 */
    lockHouse (_, params) {
      return Http.post('/mobile_api/calendar/note', params)
    },
    /** 编辑房源日历价格 */
    editPrice (_, params) {
      return Http.post('/mobile_api/calendar/edit_daily_price', params)
    }
  }
}

const RENDER_TYPE = {
  NONE: 0,
  NORMAL: 1,
  START: 2,
  END: 3,
  BOTH: 4
}

export function formatCalendarList (list) {
  list.forEach((calendar, i) => {
    if (!calendar.reservation) {
      /** 无订单的情况 */
      if (i > 0 && list[i - 1].reservation_info.check_out === calendar.date) {
        /** 订单结尾 */
        Object.assign(calendar, {
          render_type: RENDER_TYPE.END,
          previous_reservation: list[i - 1].reservation,
          previous_reservation_info: list[i - 1].reservation_info
          // thirdparty_info: PLATFORM_TYPE_INFO[list[i - 1].reservation_info.thirdparty_type]
        })
      } else {
        /** 无订单 */
        Object.assign(calendar, { render_type: RENDER_TYPE.NONE })
      }
    } else {
      /** 有订单的情况 */
      if (
        calendar.reservation_info.check_in === calendar.date &&
        i > 0 &&
        list[i - 1].reservation_info.check_out === calendar.date
      ) {
        /** 既是订单开始，又是订单结尾 */
        Object.assign(calendar, {
          render_type: RENDER_TYPE.BOTH,
          previous_reservation: list[i - 1].reservation,
          previous_reservation_info: list[i - 1].reservation_info
          // thirdparty_info: PLATFORM_TYPE_INFO[list[i - 1].reservation_info.thirdparty_type]
        })
      } else if (calendar.reservation_info.check_in === calendar.date) {
        /** 订单开始 */
        Object.assign(calendar, { render_type: RENDER_TYPE.START })
      } else {
        /** 普通订单 */
        Object.assign(calendar, { render_type: RENDER_TYPE.NORMAL })
      }
    }
  })

  return list
}



