
import Header from '@/components/header/Header.component'
import Card from 'vux/src/components/card/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { mapState, mapGetters, mapActions } from 'vuex'
export default {
name: 'OrderDetail',
components: {
Header,
Card,
Loading
},
data () {
return {
loading: false
}
},
async created () {
const query = this.$route.query
const reservation_code = query.reservation_code
this.loading = true
await this.initOrderDetail({ reservation_code: reservation_code })
this.loading = false
},
computed: {
...mapGetters('order', [
'detail'
])
},
methods: {
...mapActions({
initOrderDetail: 'order/initOrderDetail'
})
}
}



