mobile_code: '',
password: '',
password_confirmation: '',
image_code: '',

email: '',
email_code: '',
verifyCodeBtnText: '获取验证码', // 邮箱验证码

loginNum: '', // 登录账号
imgCodeSrc: '/mobile_api/user/image_code?nocache=1',
errorMob: 'true',
errorMobCod: 'true',
inputMobErr: false, // 手机号码错误
inputMoYanErr: false, // 手机验证码错误
