inputMiiErr: false, // 注册，密码第二次输入错误
inputEmErr: false, // 邮箱注册错误
inputEmYanErr: false, // 邮箱验证码错误
mobileErr: false,
err: false,
changeYd: false, // 阅读条款
nextTipMob: false, // 下一步 手机
nextTipEm: false, // 下一步 邮箱
regitMob: false, // 注册 手机
regitEm: false, // 注册 邮箱

consumMob: '',
company_name: '',
name: '',
consumEm: '',
/** 验证错误，主要由服务端返回的错误 */
backendErrors: {},
/** 邮箱能否获取验证码 */
verifyCodeBtnDisabled: false,
action_type: '',
readErr: false,
readKey: false
}
},

methods: {

changeReadKey () {
this.readKey = ! this.readKey
},

loginFn(val) {

this.threeErr = false
this.inputMiErr = false
if (this.loginType === 0) {
console.log(11)
if(this.loginNum === '' || this.password === ''){return}
let data = {'username': this.loginNum, 'password': this.password,'image_code': this.image_code }
LoginService.login(data).then((res) => {
this.error_code = ''
console.log(res)
if (200001 === res.error_code) {
this.threeNum = res.error_code;

}else if(200002 === res.error_code) {
this.modalType = 3
}

else if (res.data.errorBag) {
this.loginErrFn(res.data.errorBag);
}
else {
this.$router.push('/message/list')
}
}, (err) => {console.log(err)})
}
if (this.loginType === 1) {
console.log(112)
if(this.mobile === ''){return}
let data = {'account': this.mobile, 'login_type': 'verification_code_login' ,'code': this.mobile_code, 'image_code': this.image_code }
LoginService.login(data).then((res) => {
console.log(1)
console.log(res)
this.error_code = ''
if (200001 === res.error_code) {
this.threeNum = res.error_code;
this.backendErrors.img = res.error_msg
}else if(210002 === res.error_code) {
this.modalType = 3
}

else if (res.error_code === 0) {
this.$router.push('/message/list')
}
}, (err) => {console.log(err)})
}
},

loginErrFn (err) {
console.log('err',err)
console.log(this.threeErr)
if(err.username) {
this.backendErrors.user = err.username[0]
this.inputEmErr = true
}
if(err.password) {
this.backendErrors.pas = err.password[0]
this.inputMiErr = true
}
if(err.image_code[0]) {
console.log(err.image_code[0])
this.backendErrors.img = err.image_code[0]
this.threeErr = true
}


},


consumInfoFn () {
let data = {
'mobile': this.consumMob, 'company_name': this.company_name, 'name': this.name, 'email': this.consumEm
}
LoginService.submitOperator(data).then((res) => {
if (res.error_code === 0) {
console.log(res)
this.modalType = 1;
}
}, (err) => {console.log(err)})
},


registerFn () {

if (this.registerType) { // 手机
let data = {'account': this.mobile, 'action_type': 'register', 'code': this.mobile_code, 'password': this.password, 'password_confirm': this.password_confirmation}
LoginService.emailRegister(data).then((res) => {
if(res.error_code === 0) {
this.modalType = 3
this.nextTipMob = false
this.mobile = ''
this.password = ''
}
if (res.error_msg) {
this.backendErrors = res.error_msg
this.inputMiiErr = true
}
},(err) => {console.log(err)})
}else {
let data = {'account': this.email, 'action_type': 'register', 'code': this.email_code, 'password': this.password, 'password_confirm': this.password_confirmation}
LoginService.emailRegister(data).then((res) => {
console.log(res)
if(res.error_code === 0) {
this.modalType = 3
this.password = ''
this.nextTipMob = false
}
if(res.error_code === 200001) {
this.threeErr = ture
}
if (res.error_msg) {
this.backendErrors = res.error_msg
this.inputMiiErr = true
}
},(err) => {console.log(err)})
}
},


checkRegistFn () {
if(this.readKey) {
this.readErr = false
}else {
this.readErr = true
return
}
this.inputEmYanErr = false
this.inputMoYanErr = false
if (this.registerType) { // 手机
let data = {'account': this.mobile, 'action_type': 'register', 'code': this.mobile_code}
LoginService.mobileRegister(data).then((res) => {
console.log(res)
if(res.error_code === 0) {



this.nextTipMob = !this.nextTipMob
this.inputMoYanErr = false
this.threeErr = false;
this.mobile = ''
this.password = ''
}
if(res.error_code === 200002){return}
if (res.error_msg) {
this.inputMoYanErr = true
this.backendErrors = res.error_msg
}
}, (err) => {console.log(err)})
}else { // 邮箱
let data = {'account': this.email, 'action_type': 'register', code: this.email_code}
LoginService.mobileRegister(data).then((res) => {
console.log(res)
if(res.error_code === 0) {
this.nextTipEm = !this.nextTipEm
this.inputEmYanErr = false
this.threeErr = false;
}
if (res.error_msg) {
this.inputEmYanErr = true
this.backendErrors = res.error_msg
}
}, (err) => {console.log(err)})
}
},


toLoginFn () {
this.mobile_code = ''
this.mobile = ''
this.loginNum = ''
this.password = ''
this.threeErr = false
this.modalType = 1;
this.loginType = 0;
console.log(2)
},

toRegFn () {
this.mobile = ''
this.loginNum = ''
this.password = ''
this.threeErr = false
this.inputEmErr = false
this.inputMiErr = false
this.modalType = 0;
this.registerType =true;
console.log(1)
},
toFindPasFn () {
this.modalType = 2;
this.misOrSet = 1;
},
toSetPasFn () {

console.log('youxiang')
let data = {'account': this.mobile, 'action_type': 'reset_password', 'code': this.mobile_code}
this.mobileCopy = this.mobile
this.codeCopy = this.mobile_code
LoginService.mobileRegister(data).then((res) => {
console.log(res)
if(res.error_code === 0) {



this.misOrSet = 2;
this.threeErr = false;
this.mobile = ''
this.password = ''
this.mobile_code = ''
}




}, (err) => {console.log(err)})
},
toSucessFn () {
const data = {'account': this.mobileCopy, 'action_type': 'reset_password', 'code': this.mobile_code, 'password': this.password, 'password_confirm': this.password_confirmation}
LoginService.ResetPassword(data).then((res) => {
if(res.error_code === 0) {
this.modalType = 4
}
},(err) => console.log(err))
},
toPhoneFn () {
this.mobile = ''
this.email = ''
this.loginType = 1;
this.inputMoYanErr = false;
},

regitFn () {
this.modalType = 3
this.nextTipMob = false
},


commitFn () {
this.modalType = 4
},


nextMoFoot () {
this.nextTipMob = !this.nextTipMob
this.threeErr = false;
},


nextEmFoot () {
this.nextTipEm = !this.nextTipEm
},


changeYdF () {
this.changeYd = !this.changeYd
this.changeReadKey()
},
/**
* 获取手机验证码
* @param mobile string
* @param type 1 - 注册 2 - 找回密码
*/
getVerifyCode (val) {
if(!this.verifyCodeBtnDisabled) {
this.verifyCodeBtnDisabled =true
this.clearBackendErrors()
console.log('aaaaaa',val)
if(val === 1) {
this.action_type = 'login'
}
else if (val === 2){
this.action_type = 'reset_password'
}else {
this.action_type = 'register'
}

LoginService.getMobileCode({'account': this.mobile, 'action_type': this.action_type, 'image_code': this.image_code}).then((res) => {
console.log(res)
if (res.data && res.error_msg) {
this.threeErr = true
this.backendErrors = res.error_msg
}
if(res.error_code === 200001) {
this.threeErr = true
}
}, (err) => console.log(err))
this.subscribeBtnText() 
}
},
/**
* 获取邮箱验证码
* @param mobile string
* @param type 1 - 注册 2 - 找回密码
*/
getEmailCode () {
this.inputEmYanErr = false
if(!this.verifyCodeBtnDisabled) {
this.verifyCodeBtnDisabled = true;
LoginService.getEmailCode({'account': this.email, 'action_type': 'register', 'image_code': this.image_code}).then((res) => {
if(res.error_code === 200001) {
this.threeErr = true
}
console.log(2)
console.log(res)
}, (err) => {
console.log(err)})
this.subscribeBtnText() 
}
},


subscribeBtnText () {
this.verifyCodeBtnDisabled = true
const jishi = setInterval(() => {
if (this.timeout === 0) {
clearInterval(jishi)
}
this.$nextTick(() => {
if (this.timeout === 0) {
this.verifyCodeBtnDisabled = false;
this.verifyCodeBtnText = '获取验证码';
this.timeout = 3;
}else {
this.verifyCodeBtnText = '已发送' + (this.timeout--) + 's';
}
})
}, 1000)
},


refImgFn () {
console.log(123)
this.imgCodeSrc = this.imgCodeSrc + Date.now()
},


changeReg () {
this.mobile = ''
this.email = ''
this.nextTipMob = false
this.registerType = !this.registerType
},


getData () {
LoginService.login().then((res) => {
console.log(res)
})
},


clearBackendErrors () {
this.backendErrors = {}
}
},

created () {





},


watch: {
threeNum: {
handler () {
if (this.threeNum === 200001) {
this.threeErr = true;
}
}
},
readKey: {
handler () {
if (this.readKey) {
this.readErr = false;
}
}
},
loginNum: {
handler() {
if(0 === this.loginNum.length) {
this.inputEmErr = false
this.threeErr = false
}
}
},
mobile_code: {
handler() {
if(0 === this.loginNum.length) {
this.inputMoYanErr = false

}
}
}
}
}



