
import Header from '@/components/header/Header.component'
import { THIRDPARTY, ERROR_CODE } from '@/config'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { mapActions } from 'vuex'
export default {
name: 'PriceEdit',
components: {
Header,
XInput,
Group,
Loading
},
data () {
return {
loading: false,
form: {
house_id: '',
begin: '',
end: '',
price_list: []
},

multi: false,

multi_price: {}
}
},
async created () {

const query = this.$route.query
this.form.house_id = query.id
this.form.begin = query.check_in
this.form.end = query.check_out
this.multi = +query.multi







this.loading = true
let res = await this.getPriceCalendar({
house_id: this.form.house_id,
begin: this.form.begin,
end: this.form.end
})
this.loading = false
if (res.data && res.data.calendar) {
this.initPriceListValue(res.data.calendar)
this.multi && this.initMaxAndMinPrice(res.data.calendar)
}
},
methods: {
...mapActions({
getPriceCalendar: 'calendar/getPriceCalendar',
editPrice: 'calendar/editPrice'
}),
rangePrice (thirdparty_type, type) {
return this.multi_price[thirdparty_type] && this.multi_price[thirdparty_type][type]
},
initPriceListValue (calendar) {
const prices = calendar[0].prices.map(p => {
return {
currency_type: p.currency_type,
thirdparty_type: p.thirdparty_type,
daily_price: this.multi ? '' : p.price,
thirdparty_info: THIRDPARTY[p.thirdparty_type]
}
})
this.form.price_list = prices
},
initMaxAndMinPrice (calendar) {
const prices = calendar[0].prices
for (let i = 0; i < prices.length; i++) {
let min = prices[i].price
let max = min
for (let j = 0; j < calendar.length; j++) {
const currentPrice = calendar[j].prices[i];
if (min > currentPrice.price) {
min = currentPrice.price
} else if (max < currentPrice.price) {
max = currentPrice.price
}
}
Object.assign(this.multi_price, {
[prices[i].thirdparty_type]: {
min: min,
max: max
}
});
}
},
async save () {
const params = JSON.parse(JSON.stringify(this.form))
params.price_list.forEach(p => {
delete p.currency_type
delete p.thirdparty_info
})
params.price_list = JSON.stringify(params.price_list)
let res = await this.editPrice(params)
if (res && res.error_code === ERROR_CODE.SUCCESS) {
this.$router.go(-1)
}
}
}
}



