
import SimpleMonthCalendar from '@/components/common/Simple-Month-Calendar.component'
import { mapState, mapMutations, mapActions } from 'vuex'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import * as moment from 'moment'
export default {
name: 'CalendarOverview',
components: {
SimpleMonthCalendar,
Datetime,
Group,
Loading,
InlineLoading
},
data () {
return {
loading: false,
page_loading: false,
page: 1,
page_size: 15,
date_obj: moment(),
range: {
max: moment().add(3, 'year').format('YYYY-MM-DD'),
min: moment().subtract(1, 'year').format('YYYY-MM-DD')
}
}
},
created () {
this.fetchOverviewList(this.page)
},
computed: {
...mapState({
overview: state => state.calendar.overview
}),
date: {
get: function () {
return this.date_obj.format('YYYY-MM')
},
set: function (value) {
this.date_obj = moment(value)
}
}
},
methods: {
...mapMutations('calendar', [
'setOverview',
'appendOverviewList'
]),
...mapActions({
getOverviewList: 'calendar/getOverviewList'
}),
async fetchOverviewList (params, append = false) {
this.page_loading = true
let res = await this.getOverviewList({
page: params.page || this.page,
page_size: params.page_size || this.page_size,
date: params.data || this.date
})
if (res && res.error_code === 0) {
if (append) {
this.appendOverviewList(res.data)
} else {
this.setOverview(res.data)
}
}
this.page_loading = false
},
async onDateChange (value) {
this.fetchOverviewList({
page: 1,
page_size: this.page_size * this.page
})
},
async loadMore () {
this.loading = true
await this.fetchOverviewList({
page: ++this.page
}, true)
this.loading = false
}
}
}



