
import { Http} from '@/service/http.service'
import { HOUSE_TYPE, AMANITAIS, BED_TYPE_DESC, HOUSE_TYPE_DESC, HOUSE_SPACE_TYPE_DESC, BED_SHEET_CHANGE_DESC, PLATFORM_TYPE_INFO, CANCEL_RULE_DESC } from './config'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'


export default {
name: 'HouseDetails',
components: {
Swiper,
SwiperItem,
GroupTitle,
XButton,
Divider
},
data() {
return {
demo01_list: '',
demo01_index: 0,
swiperItemIndex: 1,
res: {}, // data
lists: [], // 图片
baseList: [],       pictureNum: 0, // 总数
amenity_list:{}, // 设施
house_room_list: {},//
price_list: [],
AMANITAIS: AMANITAIS, // 设施映射
showDesc: {one: false, two: false, three: false, four: false, five: false, six: false, seven:false}, // 介绍隐藏
homeArray: [], // 房源列表
BED_TYPE_DESC: BED_TYPE_DESC,
HOUSE_TYPE_DESC: HOUSE_TYPE_DESC,
HOUSE_SPACE_TYPE_DESC: HOUSE_SPACE_TYPE_DESC,
BED_SHEET_CHANGE_DESC: BED_SHEET_CHANGE_DESC,
PLATFORM_TYPE_INFO: PLATFORM_TYPE_INFO,
CANCEL_RULE_DESC: CANCEL_RULE_DESC
}
},
methods: {
demo01_onIndexChange (index) {
this.demo01_index = index
},

getDataFn () {

const id = window.location.hash.slice(18)
const HOSTEX_DETAIL = '/mobile_api/house_relation/hostex_detail'
const data = {'id': id}
Http.get(HOSTEX_DETAIL, data).then((res) => {
console.log(res)
if(res.error_code === 0) {
this.res = res
this.$nextTick(
this._initDataFn(),
)
}
},(err) => {console.log(err)})
},

_initDataFn() {
this.lists = this.res.data.house.house_picture_list
this.amenity_list = this.res.data.house.amenity_list
this.pictureNum = this.res.data.house.house_picture_list.length
this.homeArray = this.res.data.house.price_list
this.house_room_list = this.res.data.house.house_room_list
this.price_list = this.res.data.house.price_list
this.baseList = this.lists.map((item, index) => ({
img: item.small_url, //这个应该没问题 small_url
title: item.caption
}))
},
showDescFn (num) {
this.showDesc[num] = !this.showDesc[num]
},
goBackFn () {
this.$router.go(-1)
},
goRelationFn (id) {
this.$router.push('/house/relation?id=' + id)
},
},
created() {
const id = window.location.hash.slice(18)
this.res = JSON.parse(localStorage.getItem('data'))
console.log(this.res)






this.getDataFn();


},
watch: {
res: {
handler() {
localStorage.setItem('data',JSON.stringify(this.res))
},deep: true
}
}
}



