
import Header from '@/components/header/Header.component'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapActions } from 'vuex'
import { ERROR_CODE } from '@/config'
export default {
name: 'StatusLockHouse',
components: {
Header,
XSwitch,
XTextarea,
Group
},
data () {
return {
form: {
house_id: '',
begin: '',
end: '',
available: 1,
note: ''
}
}
},
created () {
const query = this.$route.query
this.form.house_id = query.id
this.form.begin = query.check_in
this.form.end = query.check_out
this.form.note = query.note
this.form.available = query.available
},
methods: {
...mapActions({
lockHouse: 'calendar/lockHouse'
}),
async save () {
let res = await this.lockHouse(this.form)
if (res && res.error_code === ERROR_CODE.SUCCESS) {
this.$router.go(-1)
}
}
}
}



