
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
name: 'HouseSwiper',
components: {
Swiper,
SwiperItem
},
data () {
return {
index: 0
}
},
methods: {
onIndexChange (val) {
this.$emit('onIndexChange', {
index: val,
house_id: this.houses[val].id
})
}
},
props: {
houses: {
type: Array,
default: function () {
return []
}
}
}
}



