export const Lazyload = {
  inserted: function (el) {
    const dataset = el.dataset
    const src = dataset && dataset.src
    const img = new Image()
    img.src = src
    img.onload = function () {
      el.setAttribute('src', src)
      setTimeout(() => {
      }, 0)
    }
  }
}



