import Vue from 'vue'
import Router from 'vue-router'
import store from '@/store'
import Index from '@/view/Index.view'
import Login from '@/view/login/Login.view'
import CalendarOverview from '@/view/calendar/Overview.view'
import CalendarPrice from '@/view/calendar/Price.view'
import CalendarStatus from '@/view/calendar/Status.view'
import StatusBookManual from '@/view/calendar/Status-Book-Manual.view'
import StatusLockHouse from '@/view/calendar/Status-Lock-House.view'
import PriceEdit from '@/view/calendar/Price-Edit.view'

import MessageList from '@/view/message/List.view'
import MessageDetail from '@/view/message/Detail.view'

import HouseList from '@/view/house/List.view'
import HouseDetails from '@/view/house/detail.view'
import HouseLocation from '@/view/house/relation.view'
import My from '@/view/my/my.view'
// import MyTest from '@/view/my/test.view'

import OrderList from '@/view/order/List.view'
import OrderDetail from '@/view/order/detail.view'

import Account from '@/view/setting/Account.view'
import Third from '@/view/third/Third.view'
import Modify from '@/view/password/modify.view'

Vue.use(Router)

/** 日历相关路由 */
const calendar = [
  {
    path: '/calendar/overview',
    name: 'CalendarOverview',
    component: CalendarOverview
  },
  {
    path: '/calendar/price',
    name: 'CalendarPrice',
    component: CalendarPrice
  },
  {
    path: '/calendar/status',
    name: 'CalendarStatus',
    component: CalendarStatus
  },
  {
    path: '/calendar/status-book-manual',
    component: StatusBookManual
  },
  {
    path: '/calendar/status-lock-house',
    component: StatusLockHouse
  },
  {
    path: '/calendar/price-edit',
    component: PriceEdit
  }
]
/** 房源相关路由 */
const houses = [
  {
    path: '/house/list',
    name: 'HouseList',
    component: HouseList
  },
  {
    path: '/house/detail',
    name: 'HouseDetails',
    component: HouseDetails
  },
  {
    path: '/house/relation',
    name: 'HouseRelation',
    component: HouseLocation
  }
]
/** 系统我的路由 */
const my = [
  {
    path: '/my',
    name: 'My',
    component: My
  }
  // {
  //   path: '/my/test',
  //   name: 'MyTest',
  //   component: MyTest
  // }
]

/** 订单相关路由 */
const orders = [
  {
    path: '/order/list',
    component: OrderList
  },
  {
    path: '/order/detail',
    component: OrderDetail
  }
]
/** 设置，账号管理相关路由 */
const settings = [
  {
    path: '/setting',
    name: 'Account',
    component: Account
  }
]
/** 第三方设置，账号管理相关路由 */
const thirds = [
  {
    path: '/third',
    name: 'Third',
    component: Third
  }
]
/** 消息相关路由 */
const messages = [
  {
    path: '/message/list',
    component: MessageList
  },
  {
    path: '/message/detail',
    component: MessageDetail
  }
]
// 登录
const login = [
  {
    path: '/login',
    name: 'Login',
    component: Login
  }
]
// 修改密码
const modify = [
  {
    path: '/modify',
    name: 'Modify',
    component: Modify
  }
]

const index = [
  {
    path: '/',
    component: Index,
    children: [
      { path: '', component: HouseList },
      ...houses,
      ...calendar,
      ...my,
      ...messages,
      ...settings,
      ...orders,
      ...thirds,
      ...modify
    ]
  }
]

const router = new Router({
  routes: [
    ...index,
    ...login
  ]
})

router.beforeEach((to, from, next) => {
  store.commit('UPDATE_LOADING', true)
  next()
})

router.afterEach((to, from, next) => {
  store.commit('UPDATE_LOADING', false)
})
export default router



