
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { Http } from '@/service/http.service'
import Footer from '@/components/footer/Footer.component'

export default {
name: 'HouseList',
data() {
return {
lists: [],
results: [],
value: '',
page: 1, // 页码
page2: 0,
total: 0, // zongshu
lists2: [{}], // 判断获取数量不为0
page_size: 100, // 每页10条
key: 0
}
},
components: {
Search,
Group,
Cell,
XButton,
Footer
},
methods: {
goThirdFn () {
this.$router.push('/third')
},

setFocus () {
this.$refs.search.setFocus()
},
resultClick (item) {
console.log('you click the result item: ' + JSON.stringify(item))
},
getResult (val) {
console.log('on-change', val)
this.results = val ? getResult1(this.value) : []
},
onSubmit () {
this.$refs.search.setBlur()
console.log('你好')
this.key = 1;
},
onFocus () {
console.log('on focus')
},
onCancel () {
console.log('on cancel')
this.key = 0
},


getDataFn () {
const HOSTEX_HOUSE_LIST = '/mobile_api/house_relation/hostex_house_list'
const data = {'page': this.page, 'page_size': this.page_size}
Http.get(HOSTEX_HOUSE_LIST, data).then((res) => {
console.log('res:', res)
this.lists = res.data.list
this.total = res.data.total
}, (err) => {console.log('err:', err)})
},
getMoreDataFn () {
console.log('get more')
const HOSTEX_HOUSE_LIST =       '/mobile_api/house_relation/hostex_house_list'
this.page2 = this.page2 + 1
const data = {'page': this.page2, 'page_size': this.page_size}
Http.get(HOSTEX_HOUSE_LIST, data).then((res) => {

if(res.error_code === 0) {
this.lists = this.lists.concat(res.data.list)
this.total = res.data.total
}

}, (err) => {console.log('err:', err)})

},

refresh (done) { //这是向下滑动的时候请求最新的数据
console.log('下拉')
const ss = function () {
done()
this.getDataFn(done)
this.page2 = 1
}.bind(this)
setTimeout(ss, 1000)
},

infinite() {
console.log(' 加载')
if(this.lists.length === this.total) {
setTimeout(() => {
console.log('停止')
this.$refs.scroller.finishInfinite(2)
}, 0)
return;
}
if(this.lists.length < this.total) {
const ss = function () {
setTimeout(() => {
this.getMoreDataFn()
}, 0)
done()
}.bind(this)
setTimeout(ss, 1000)
}

},
goCalendarFn (id) {
this.$router.push('/calendar/status?id=' + id)
},
goDetailsFn (id) {
this.$router.push('/house/detail?id=' + id)
}
},
created() {
this.getDataFn()
},
computed: {
listFilter() {
if( this.key === 1){
const list = this.lists.filter((item) => (item.house_descriptions[0].title.indexOf(this.value) !== -1))
return list
}
return this.lists
}
},
watch: {












}
}














