import { Http } from '@/service/http.service'
import { STATUS, THIRDPARTY } from '@/config'

export default {
  namespaced: true,
  state: {
    _orders: [],
    _detail: {},
    total: 0
  },
  getters: {
    orders (state) {
      return state._orders.map(order => {
        const status = order.status
        const type = order.thirdparty_type
        const priceItem = order.price_items.find(p => p.type === 'ACCOMMODATION')
        return {
          ...order,
          ...{
            status_name: STATUS[status] ? STATUS[status].name : '',
            thirdparty_info: THIRDPARTY[type] ? THIRDPARTY[type] : {},
            total_price_currency: priceItem ? priceItem.currency : '',
            total_price: priceItem ? priceItem.price : 0
          }
        }
      })
    },
    detail (state) {
      const detail = state._detail
      return {
        ...detail,
        ...{
          status_name: STATUS[detail.status] ? STATUS[detail.status].name : ''
        }
      }
    }
  },
  mutations: {
    setOrders (state, payload) {
      state._orders = payload
    },
    setOrderByIndex (state, payload) {
      const { order, index } = payload
      state._orders.splice(index, 1, order)
    },
    setTotal (state, payload) {
      state.total = payload
    },
    setOrderDetail (state, payload) {
      state._detail = payload
    },
    appendOrders (state, payload) {
      state._orders = [...state._orders, ...payload]
    }
  },
  actions: {
    async initOrders ({ state, commit }, params) {
      const p = { page: 1, page_size: 15, ...params }
      let res = await Http.get('/mobile_api/reservation/order_list', p)
      if (res.data && res.data.list) {
        if (p.page === 1) {
          commit('setOrders', res.data.list)
        } else {
          commit('appendOrders', res.data.list)
        }
        commit('setTotal', res.data.total)
      }
    },
    async initOrderDetail ({ state, commit }, params) {
      let res = await Http.get('/mobile_api/reservation/order_detail', params)
      if (res.data && res.data.reservation) {
        commit('setOrderDetail', res.data.reservation)
      }
    },
    declineOrder (_, params) {
      return Http.post('/mobile_api/reservation/reservation_decline', params)
    },
    approveOrder (_, params) {
      return Http.post('/mobile_api/reservation/reservation_approve', params)
    }
  }
}



