import Header from '@/components/header/Header.component'
import Footer from '@/components/footer/Footer.component'
import { mapState } from 'vuex'

export default {
  name: 'Index',
  components: {
    Header,
    Footer
  },
  computed: {
    ...mapState({
      unread: state => state.unread
    }),
    hasTabbar () {
      return [
        '/house/list',
        '/message/list',
        '/calendar/overview',
        '/my'
      ].includes(this.$route.path)
    }
  }
}



