import * as moment from 'moment'
export default {
  name: 'Calendar',
  data () {
    return {
      locale: 'zh-cn',
      weeksCalendar: [],
      listOfWeekName: [],
      listOfMonthName: [],
      listOfYearName: [],
      value: new Date(),
      // showYear: moment().year(),
      // showMonth: moment().month()
    }
  },
  props: {
    showWeekBar: {
      type: Boolean,
      default: false
    },
    showMonthBar: {
      type: Boolean,
      default: true
    },
    showYear: {
      type: Number,
      default: moment().year()
    },
    showMonth: {
      type: Number,
      default: moment().month()
    },
    data: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  created () {
    this.buildCalendar()
    if (this.data.length) {
      this.injectData()
    }
  },
  beforeUpdate () {
    this.injectData()

  },
  methods: {
    buildCalendar: function () {
      moment.locale(this.locale);
      this.listOfYearName = this.generateYears(this.showYear);
      this.listOfWeekName = moment.weekdaysMin();
      this.listOfMonthName = moment.months();
      const date = moment(this.value).year(this.showYear).month(this.showMonth);
      this.weeksCalendar = this.buildMonth(date);
    },
    generateYears: function (year) {
      const listOfYears = [];
      for (const i of Array.from(Array(20).keys())) {
        listOfYears.push(i - 10 + year);
      }
      return listOfYears;
    },
    buildMonth: function (d) {
      const weeks = [];
      const rawDate = this.removeTime(d);
      // 获取本月1号所在星期的第一天的日期 如 28 29 30 31 1 2 3 里就是28这天
      const start = rawDate.clone().date(1).day(0);
      const month = rawDate.clone();
      let done = false;
      const date = start.clone();
      let count = 0;
      while (!done) {
        weeks.push({
          days: this.buildWeek(date.clone(), month)
        });
        date.add(1, 'w');
        done = ++count > 4;
      }
      return weeks;
    },
    buildWeek: function (date, month) {
      const days = [];
      for (let i = 0; i < 7; i++) {
        days.push({
          number: date.date(),
          isLastMonth: date.month() < month.month(),
          isNextMonth: date.month() > month.month(),
          isPastDay: date.isBefore(new Date(), 'day'),
          isCurrentDay: date.isSame(new Date(), 'day'),
          isSelectedDay: date.isSame(this.value, 'day'),
          title: date.format('YYYY-MM-DD'),
          date: date,
          injectedData: null
        });
        date = date.clone();
        date.add(1, 'd');
      }
      return days;      
    },
    removeTime: function (date) {
      return date.hour(0).minute(0).second(0).millisecond(0);
    },
    prevYear: function () {},
    nextYear: function () {},
    prevMonth: function () {},
    nextMonth: function () {},
    injectData () {
      for (let i = 0,l = this.weeksCalendar.length; i < l; i++) {
        for (let j = 0, dl = this.weeksCalendar[i].days.length; j < dl; j++) {
          const current = this.weeksCalendar[i].days[j]
          current.injectedData = this.data.find(d => d.date === current.title)
        }
      }
    }
  }
}



