import * as moment from 'moment'
import Calendar from '@/components/common/Calendar.component'
export default {
  name: 'MonthCalendar',
  components: {
    Calendar
  },
  data () {
    return {
      selected: []
    }
  },
  beforeUpdate () {
    if (this.selected.length) {
      this.selected = []
    }
  },
  methods: {
    isOrder (data) {
      return data && data.render_type === 1
    },
    isOrderStart (data) {
      return data && [2, 4].includes(data.render_type)
    },
    isOrderEnd (data) {
      return data && [3, 4].includes(data.render_type)
    },
    isLocked (data) {
      return data && data.available === 0
    },
    isPast (data) {
      return data && data.isPastDay
    },
    isShowPrice (data) {
      return this.showPrice && !(this.isOrder(data) || this.isOrderStart(data) || this.isLocked(data))
    },
    isClickable (data) {
      const injectedData = data.injectedData
      /** 先判断数据是否已经注入进来，如果还未注入，则不允许点击 */
      return this.data.length && !(this.isPast(data) || this.isOrder(injectedData) || this.isOrderStart(injectedData))
    },
    showOrderUserName (data) {
      const injectedData = data.injectedData
      return injectedData.reservation_info && data.date.clone().subtract(1, 'd').format('YYYY-MM-DD') === injectedData.reservation_info.check_in
    },
    orderUserName (data) {
      return data.reservation_info && data.reservation_info.thirdparty_customer && data.reservation_info.thirdparty_customer.name
    },
    price (data) {
      const map = {
        'CNY': '￥',
        'USD': '$',
        'JPY': '￥'
      }
      const price = data && data.prices && data.prices.find(p => p.thirdparty_type == +this.source)
      // console.log(price)
      // const price = data && data.prices && data.prices[0]
      return price ? `${map[price.currency_type]}${price.price}` : ''
    },
    userHeader (data) {
      return data.reservation_info && data.reservation_info.thirdparty_customer && data.reservation_info.thirdparty_customer.photo
    },
    handleClick (data) {
      if (!this.isClickable(data)) {
        return
      }
      const date /** Moment */ = data.date
      const first /** Moment */ = this.selected[0] && this.selected[0].date.clone()
      const last /** Moment */ = this.selected[this.selected.length - 1] && this.selected[this.selected.length - 1].date.clone()
      if (this.selected.length === 0) {
        this.selected.push(data)
      }
      else if (first.subtract(1, 'd').isSame(date, 'd')) {
        this.selected.unshift(data)
      }
      else if (last.add(1, 'd').isSame(date, 'd')) {
        this.selected.push(data)
      }
      else if (first.isSame(date, 'd')) {
        this.selected.shift()
      }
      else if (last.isSame(date, 'd')) {
        this.selected.pop()
      }
      else {
        this.selected.splice(0)
        this.selected.push(data)
      }
      this.$emit('onDateSelected', this.selected)
    },
  },
  props: {
    showPrice: {
      type: Boolean,
      default: false
    },
    date: {
      type: Object,
      default: function () {
        return moment()
      }
    },
    data: {
      type: Array,
      default: function () {
        return []
      }
    },
    source: [String, Number]
  }
}



