import { Http } from '@/service/http.service'
import { THIRDPARTY, ERROR_CODE } from '@/config'
import * as moment from 'moment'

export class Thread {
  constructor (options) {
    this.id = options.id
    this.last_message_time = options.last_message_time
    this.unread_count = options.unread_count
    // this.thirdparty_type = options.thirdparty_type || 0
    this.text_preview = options.text_preview
    this.thirdparty_tenant_customer = options.thirdparty_tenant_customer || {}
  }

  get relative_time () {
    return moment(this.last_message_time).fromNow()
  }
  get thirdparty_name () {
    return THIRDPARTY[this.thirdparty_tenant_customer.thirdparty_type]
  }
}

export class Message {
  constructor (options) {
    this.id = options.id
    this.origin_create_time = options.origin_create_time
    this.display_type = options.display_type || 'Text'
    this.display = options.display
    this.message = options.message
    this.thirdparty_customer_type = options.thirdparty_customer_type // 0-guest | 1-host
    this.send_status = true || options.send_status // 发送状态 true 成功  false 失败
  }
  get time () {
    return moment(this.origin_create_time).format('MM-DD HH:mm')
  }
}

export default {
  namespaced: true,
  state: {
    threads: [],
    messages: [],
    customer: null,
    house_id: 0
  },
  mutations: {
    setThreads (state, payload) {
      // state._threads = payload
      state.threads = payload.map(p => new Thread(p))
    },
    updateThread (state, payload) {
      const { index, thread } = payload
      state.threads.splice(index, 1, thread)
    },
    unshiftThread (state, payload) {
      state.threads.unshift(payload)
    },
    setCustomer (state, payload) {
      state.customer = payload
    },
    setHouseId (state, payload) {
      state.house_id = payload
    },
    setMessages (state, payload) {
      state.messages = payload.map(p => new Message(p)).reverse()
    },
    appendMessage (state, payload) {
      state.messages.push(payload)
    },
    updateMessage (state, payload) {
      const index = state.messages.findIndex(message => message === payload)
      if (index !== -1) {
        state.messages.splice(index, 1, payload)
      }
    },
    clearMessages (state) {
      state.messages = []
    },
    clearCustomer (state) {
      state.customer = null
    }
  },
  actions: {
    async getThreads ({ state, commit }, params = { page: 1 }) {
      let res = await Http.get('/mobile_api/chat/chat_list', params)
      if (res && res.data && res.data.list) {
        commit('setThreads', res.data.list)
        commit('UPDATE_UNREAD', res.data.total_list.total_count, { root: true })
      }
    },
    async getMessages ({ state, commit }, params) {
      let res = await Http.get('/mobile_api/chat/chat_detail', params)
      if (res && res.data && res.data.list) {
        commit('setMessages', res.data.list)
        commit('setCustomer', res.data.customer)
        commit('setHouseId', res.data.house_info && res.data.house_info.house_id)
      }
    },
    async sendMessage ({ state, commit }, params) {
      return Http.post('/mobile_api/chat/send_text', params)
      // if (res && res.error_code === ERROR_CODE.SUCCESS) {
      //   const message = new Message({
      //     origin_create_time: moment(),
      //     thirdparty_customer_type: 1,
      //     message: params.message
      //   })
      //   commit('appendMessage', message)
      // }
    },
    // async sendMessage ({ state, commit }, params) {
    //   let res = await Http.post('/mobile_api/chat/send_text', params)
    //   if (res && res.error_code === ERROR_CODE.SUCCESS) {
    //     const message = new Message({
    //       origin_create_time: moment(),
    //       thirdparty_customer_type: 1,
    //       message: params.message
    //     })
    //     commit('appendMessage', message)
    //   }
    // },
    async getTotalUnreadCount ({ commit }, params) {
      let res = await Http.get('/mobile_api/chat/unread_inquiry_count')
      if (res && res.data) {
        commit('UPDATE_UNREAD', res.data.total, { root: true })
      }
    }
  }
}



