import { Http } from '@/service/http.service'

const CODE_API = '/mobile_api/user/code'
const SUBMIT_OPERATOR_API = '/mobile_api/user/submit_operator'
const REGISTER_API = '/mobile_api/user/register'
const RESET_PASSWORD_API = '/mobile_api/user/reset_password'
const MOBILE_RESET_PASSWORD_API = '/api/operator_user/mobile_reset_password'
const SEND_RESET_EMAIL_API = '/api/operator_user/send_reset_email'
// const EMAIL_RESET_PASSWORD_API = '/api/operator_user/email_reset_password'
// const SUBMIT_OPERATOR_API = '/api/operator_user/submit_operator'
const LOGIN_API = '/mobile_api/user/login'
const LOGOUT_API = '/api/operator_user/logout'
const CHECK_CODE_API = '/mobile_api/user/check_code'

export const LoginService = {

  // constructor(private http: HttpClient) { },

  getMobileCode (data) {
    return Http.get(
      CODE_API, data
    )
  },

  getEmailCode (data) {
    return Http.get(
      CODE_API, data
    )
  },

  /**
   * 验证验证码接口
   * @param params MobileRegisterParams
   */
  mobileRegister (MobileRegisterParams) {
    return Http.post(
      CHECK_CODE_API,
      MobileRegisterParams
    )
  },
  /**
   * 注册接口
   * @param params EmailRegisterInterface
   */
  emailRegister (EmailRegisterParams) {
    return Http.post(
      REGISTER_API,
      EmailRegisterParams
    )
  },
  /**
   * 完善信息
   */
  comsumRegister (ComsumInfoRegisterParams) {
    return Http.post(

    )
  },

  // 登录状态下修改密码
  resetPasswordOnline (IResetPassword) {
    return Http.post(
      RESET_PASSWORD_API,
      IResetPassword
    )
  },

  /**
   * 手机重置密码
   * @param params MobileRegisterInterface
   */
  mobileResetPassword (MobileResetPWParams) {
    return Http.post(
      MOBILE_RESET_PASSWORD_API,
      MobileResetPWParams
    )
  },
  /**
   * 发送重置密码邮件
   * @param params ResetPasswordInterface
   */
  sendResetEmail (SendResetEmialParams) {
    return Http.post(
      SEND_RESET_EMAIL_API,
      SendResetEmialParams
    )
  },
  /**
   * 邮箱重置密码
   * @param params ResetPasswordInterface
   */
  ResetPassword (EmailResetPWParams) {
    return Http.post(
      RESET_PASSWORD_API,
      EmailResetPWParams
    )
  },
  // 完善信息
  submitOperator (SubmitOperatorParams) {
    return Http.post(
      SUBMIT_OPERATOR_API,
      SubmitOperatorParams
    )
  },

  /**
   * 登录接口
   * @param params LoginInterface
   */
  login (LoginParams) {
    return Http.post(
      LOGIN_API,
      LoginParams
    )
  },
  /**
   * 退出接口
   */
  logout () {
    return Http.post(LOGOUT_API, null)
  }

}



