
import { Http } from '@/service/http.service'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'
import Search from 'vux/src/components/search/index.vue'

import { PLATFORM_TYPE_INFO } from './config'
export default {
name:'HouseRelation',
directives: {
TransferDom
},
components: {
XButton,
Confirm,
Search
},
data() {
return {
list: [],
lists: [], // 未绑定列表
results: [],
value: '',
modeType: 0,
page: 1, // 页码
page2: 0,
total: 1, // zongshu
lists2: [{}], // 判断获取数量不为0
page_size: 100, // 每页10条
key: 0,
data: {},
showDelete: false,
show: false,
show2: false,
length: '',
id: '',
key: '',
house_price_id: '',
thirdType: '',
thirdparty_house_list: [],
PLATFORM_TYPE_INFO: PLATFORM_TYPE_INFO,
error_404: false
}
},
methods: {
goDetailFn () {
console.log(this.data.list[0].house_id)
this.$router.push('/house/detail?id=' + this.data.list[0].house_id)
},
goBackFn () {
this.$router.go(-1)
},

changeModeType () {
this.modeType = 0
this.error_404 = false
},


setFocus () {
this.$refs.search.setFocus()
},
resultClick (item) {

},
getResult (val) {
console.log('on-change', val)
this.results = val ? getResult1(this.value) : []
},
onSubmit () {
this.$refs.search.setBlur()
console.log('你好')
this.key = 1;
},
onFocus () {
console.log('on focus')
},
onCancel () {
console.log('on cancel')
this.key = 0
},

onConfirm () {
console.log('on confirm')
this.houseDeleteFn()


},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
},

getThirdFn () {

},
goAddFn () {
this.modeType = 1
this.getUnDataFn()
},

addHouseFn (val) {
this.error_404 = false
const list = this.data.list
list.push(val)
const j =JSON.stringify(list)
const BIND_RELATION = '/mobile_api/house_relation/bind_relation'
const id = window.location.hash.slice(20)
const data = {'house_id': id, 'price_list': j}
Http.post(BIND_RELATION, data).then((res) => {
console.log(res)
if(res.error_code === 0) {
this.getDataFn()
}

},(err) => {console.log(err)})
console.log('add')
console.log(val)
},

getId () {
return window.location.hash.slice(20)
},

getDataFn () {
console.log('1')
const BIND_PLATFORM = '/mobile_api/house_relation/bind_platform'
const id = window.location.hash.slice(20)
const data = {'house_id': id}
Http.get(BIND_PLATFORM, data).then((res) => {
console.log(res)
if(res.error_code === 0) {
this.data = res.data
if(res.data.list.length === 0) {
this.$router.go('/house/list')
}else{
this.$nextTick(this._initDataFn())
}
}else {
console.log(res.error_msg)
}
},(err) => console.log(err))
},

getUnDataFn () {
const THIRDPARTY_HOUSE_LIST = '/mobile_api/house_relation/thirdparty_house_list'
const data = {'thirdparty_types': '1'}
console.log(data)
Http.get(THIRDPARTY_HOUSE_LIST, data).then((res) => {
console.log('unres:', res)
if(res.error_code === 0) {
this.lists = res.data.list
if(res.data.list.length === 0) {this.error_404 = true}
}else {
alert(res.error_msg)
}
},(err) => console.log(err))
},


houseDeleteFn () {
console.log(this.data.list.length)
const UNBIND_RELATION = '/mobile_api/house_relation/unbind_relation'
const data = {'house_id': this.id, 'house_price_id': this.house_price_id }
Http.post(UNBIND_RELATION, data).then((res) => {
if(res.error_code === 0) {

this.getDataFn()
}else {
alert(res.error_msg)
}
},(err) => console.log(err))
},


_initDataFn () {
this.modeType = 0
this.length = this.data.list.length
this.thirdType = this.data.list.map((item, index) => item.thirdparty_type).join()
},













adjustFn () {
console.log('1')
this.showDelete = !this.showDelete;
},
showPlugin (val,val2) {
if(this.data.list.length === 1) {
this.show = true
}else {
this.show2 = true
}
console.log(val,val2)
this.id = val
this.house_price_id = val2
}
},
created() {
this.getDataFn()

},
computed: {
listsFilter() {
if(this.key === 1) {
const list = this.lists.filter((item, index) => (item.title.indexOf(this.value) !== -1))
return list
}
return this.lists
}
}
}



