
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Badge from 'vux/src/components/badge/index.vue'

export default {
name: 'Footer',
data () {
return{
hash: '',
items: [
{
link: '/message/list',
icon: require('../../assets/message.png'),
icon_active: require('../../assets/message_activated.png'),
text: '消息',
showBadge: true
},
{
link: '/calendar/overview',
icon: require('../../assets/status.png'),
icon_active: require('../../assets/status_activated.png'),
text: '房态'
},
{
link: '/house/list',
icon: require('../../assets/house.png'),
icon_active: require('../../assets/house_activated.png'),
text: '房源'
},
{
link: '/my',
icon: require('../../assets/my.png'),
icon_active: require('../../assets/my_activated.png'),
text: '我的'
},
]
}
},
components: {
Tabbar,
TabbarItem,
Badge
},
props: {
unread: {
type: Number,
default: 0
}
},
computed: {
stringifiedUnread () {
return this.unread === 0 ? '' : `${this.unread}`
}
},
methods: {
},
created() {
},
}



