_syncerCommitFn () {
if(this.syncerKey === 0) {
this.text = '同步成功'
const ss = function () {
this.show1 = false
this.modifiType = 4
clearTimeout(s)
}.bind(this)
const s = setInterval(ss, 1000)
}else if(this.syncerKey === 1) {
this.text = '同步失败'
const ss = function () {
this.show1 = false
}.bind(this)
setTimeout(ss, 1000)
}
