
import * as moment from 'moment'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import Header from '@/components/header/Header.component'
import HouseSwiper from '@/components/common/Swiper.component'
import MonthCalendar from '@/components/common/Month-Calendar.component'
import { THIRDPARTY } from '@/config'
import { mapState, mapMutations, mapActions } from 'vuex'
export default {
name: 'MultiCalendars',
components: {
Header,
Group,
Selector,
Loading,
PopupPicker,
HouseSwiper,
MonthCalendar
},
data () {
return {
loading: false,
house_id: 0,
common: {
minDate: moment().subtract(1, 'year'), /** 日历允许的最小日期 */
maxDate: moment().add(3, 'year'),
loadCount: 3, /** 每次加载日历的个数 */
},
selectedDates: [],

filter: {
title: '价格来源',
source: '',
options: [{}]


}
}
},
props: {
type: {
type: Number,
default: 1 // 1 - status | 2 - price
}
},
async created () {
this.house_id = this.$route.query.id
this._resetCalendars()

await this.initHouses()
this._fixSwiperIndex()

},
computed: {
...mapState({
houses: state => state.calendar.houses,
calendars: state => state.calendar.calendars
})
},
methods: {
...mapMutations('calendar', [
'increaseByPush',
'increaseByUnshift',
'removeCalendarsData'
]),
...mapActions({
initCalendars: 'calendar/initCalendars',

getCalendarHouseList: 'calendar/getCalendarHouseList',
getCalendarDataByMonths: 'calendar/getCalendarDataByMonths'
}),
async onIndexChange (data) {
this.house_id = data.house_id
this._resetCalendars()

await this.initCalendarsData()
this._refreshFilterFormOptions()
},
onDateSelected (data) {
this.selectedDates = data
},
onSourceChange (data) {
this.filter.source = data

},
bookManual () {
const { id, check_in, check_out } = this._assembleParams()
this.$router.push({
path: '/calendar/status-book-manual',
query: { id, check_in, check_out }
})
},
lockHouse () {
const { id, check_in, check_out, note, available } = this._assembleParams()
this.$router.push({
path: '/calendar/status-lock-house',
query: { id, check_in, check_out, note, available }
})
},
editPrice () {
const { id, check_in, check_out, multi } = this._assembleParams()
this.$router.push({
path: '/calendar/price-edit',
query: { id, check_in, check_out, multi }
})
},
_assembleParams () {
const multi = this.selectedDates.length === 1 ? 0 : 1
return {
id: this.house_id,
check_in: this.selectedDates.length ? this.selectedDates[0].date.format('YYYY-MM-DD') : '',
check_out: this.selectedDates.length ? this.selectedDates[this.selectedDates.length - 1].date.format('YYYY-MM-DD') : '',
note: multi ? '' : this.selectedDates[0].injectedData.note,
available: multi ? 1 : this.selectedDates[0].injectedData.available,
thirdparties: this.selectedDates[0].injectedData.prices.map(p => p.thirdparty_type).join(','),
multi: multi
}
},

async _fixSwiperIndex () {
const index = this.houses.findIndex(h => h.id == this.house_id)
this.$refs.swiper.index = index

/** index为0不会触发swiper的index的change事件，需手动获取一次数据 */
if (index === 0) {
await this.initCalendarsData()
this._refreshFilterFormOptions()
}
},
_refreshFilterFormOptions () {
let prices;
try {
prices = this.calendars[0].data[0].prices
} catch (e) {
prices = []
}

const options = []
prices.forEach(p => {
options.push({
key: `${p.thirdparty_type}`,
value: THIRDPARTY[p.thirdparty_type] ? THIRDPARTY[p.thirdparty_type].name : '未知'
})
})


this.filter.options = options
this.filter.source = options[0].key

},
async initHouses () {
this.loading = true
await this.getCalendarHouseList()
this.loading = false
},
async initCalendarsData () {
await this.getCalendarDataByMonths({
house_id: this.house_id,
begin: this._monthFormat(this.calendars[0].month),
end: this._monthFormat(this.calendars[this.calendars.length - 1].month)
});

},
refresh (done) {
const cb = function () {
this._loadMorePrevMonths()
done()
this.getCalendarDataByMonths({
begin: this._monthFormat(this.calendars[0].month),
end: this._monthFormat(this.calendars[2].month),
house_id: this.house_id
})
}.bind(this)

setTimeout(cb, 1000)
},
infinite (done) {
const cb = function () {
this._loadMoreNextMonths()
done()
this.getCalendarDataByMonths({
begin: this._monthFormat(this.calendars[this.calendars.length - 3].month),
end: this._monthFormat(this.calendars[this.calendars.length - 1].month),
house_id: this.house_id
})
}.bind(this)

setTimeout(cb, 1000)
},
_loadMorePrevMonths () {
let firstMonth = this.calendars[0] && this.calendars[0].month
for (let i = 0; i < this.common.loadCount; i++) {
if (firstMonth.isBefore(this.common.minDate)) {
setTimeout(() => {
this.$refs.scroller.finishPullToRefresh()
}, 0)
return
}
const month = firstMonth.clone().subtract(1, 'M')

this.increaseByUnshift(month)
firstMonth = month
}
},
_loadMoreNextMonths () {
let length = this.calendars.length,
lastMonth = this.calendars[length - 1] && this.calendars[length - 1].month
for (let i = length; i < length + this.common.loadCount; i++) {
if (lastMonth.isAfter(this.common.maxDate)) {
setTimeout(() => {
this.$refs.scroller.finishInfinite(true)
}, 0)
return
}
const month = lastMonth.clone().add(1, 'M')

this.increaseByPush(month)
lastMonth = month
}
},
_resetCalendars () {
this.selectedDates = []
this.removeCalendarsData()
this.initCalendars()
this.$refs.scroller && this.$refs.scroller.scrollTo(0, 0)
},
_monthFormat (date) {
return date.format('YYYY-MM')
}
}
}



