
import Header from '@/components/header/Header.component'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapActions } from 'vuex'
import { Validator } from '@/service/validate.service'
import { ERROR_CODE } from '@/config'
export default {
name: 'StatusBookManual',
components: {
Header,
XInput,
Datetime,
Group
},
data () {
return {
form: {
house_id: '',
name: '',
mobile: '',
check_in: '',
check_out: '',
ID_number: '',
price: ''
}
}
},
created () {
const query = this.$route.query
this.form.house_id = query.id
this.form.check_in = query.check_in
this.form.check_out = query.check_out
},
methods: {
...mapActions({
bookManual: 'calendar/bookManual'
}),
async save () {
if (!this._checkValidity()) {
return
}
let res = await this.bookManual(this.form)
if (res && res.error_code === ERROR_CODE.SUCCESS) {
this.$router.go(-1)
}
},
_checkValidity () {
const form = this.form
return form.house_id && form.name && Validator.mobile(form.mobile) && form.check_in && form.check_out && form.price
}
}
}



