 ($$v) {_vm.showScrollBox=$$v},expression:"showScrollBox"}},[_c('p',{staticClass:"dialog-title"},[_vm._v("百居易网站用户使用协议")]),_vm._v(" "),_c('div',{staticClass:"img-box",staticStyle:{"height":"500px","padding":"15px 0","overflow":"scroll","-webkit-overflow-scrolling":"touch"}},[_c('p',[_vm._v("百居易网站www.hostex.com（以下可简称“百居易”或网站）由小格创新科技（北京）有限公司（以下简称“小格创新”）运营，您通过注册程序阅读本《百居易用户服务协议》（以下简称“服务协议”）并点击“同意”或“注册”按钮成为百居易用户（以下可简称“用户”）或以“百居易”允许的其他方式实际浏览“百居易”、使用“百居易”功能，表示您已充分阅读、正确理解并完全接受本协议的全部内容。若您不同意本协议内容、无法准确理解条款的解释或对本协议的条款存在任何疑问或异议，应立即停止注册、使用“百居易”提供的服务。")]),_vm._v(" "),_c('h4',[_vm._v("\n                  一、协议的效力\n                ")]),_vm._v(" "),_c('p',[_vm._v("\n                  1．协议中“百居易”，为小格创新科技（北京）有限公司旗下产品和服务，是根据相关法律、法规为拥有房屋所有权或使用权（含转租权）的房东及有租住需求的房客提供在线交易的技术服务平台。"),_c('br'),_vm._v(" \n                  2．本协议内容包括协议正文及所有“百居易”已经发布的或将来可能发布的各类规则。所有规则为本协议不可分割的组成部分，与协议正文具有同等法律效力。除另行明确声明外，任何“百居易”及其关联公司提供的服务（以下称为“百居易”服务）均受本协议约束。"),_c('br'),_vm._v("\n                  3．使用“百居易”服务前，您承诺接受并遵守本协议的约定。如您不同意本协议的约定，您应立即停止注册程序或停止使用“百居易”服务。您应当在使用“百居易”服务之前认真阅读本协议内容，您使用“百居易”服务的行为即视为您与“百居易”缔结本协议，本协议即对您产生约束，届时您不应以未阅读本协议的内容或者未获得“百居易”对您问询的解答等理由，主张本协议无效，或要求撤销本协议。"),_c('br'),_vm._v("\n                  4．“百居易”有权根据国家法律法规的更新、产品和服务规则的调整需要，不时地制订、变更或修改本协议及（或）其相关各项条款、各类规则。一旦条款内容发生变动，“百居易”会在平台上对变更后的条款进行公布，将不再单独通知您。在使用“百居易”平台服务时，您有必要对最新的“百居易”服务协议条款进行仔细阅读，如您继续使用“百居易”服务，即表示您接受已经修订的协议。如您不同意相关变更，应当立即停止使用“百居易”的各项服务。经修订的协议一经公布后，立即自动生效。各类规则会在发布后生效，亦成为本协议的一部分，当发生有关争议时，以最新的服务协议为准。"),_c('br'),_vm._v("\n                  5．对于您提供的“百居易”资料及数据信息，您授予“百居易”全球通用的、长期的、免费的许可使用权利。您同意，“百居易”有权（全部或部分地）使用、复制、修订、改写、发布、翻译、分发、执行和展示您的资料数据（包括但不限于注册资料、行为数据及全部展示于“百居易”平台的各类信息）或制作其派生产品，并以现在已知或日后开发的形式、媒体或技术，将上述信息纳入其它产品内。"),_c('br'),_vm._v("\n                  6．您接受并使用“百居易”各项服务时，除遵守本协议外，仍需遵守此服务其它各类相关规则，且您的使用行为受其约束。\n                ")]),_vm._v(" "),_c('h4',[_vm._v("二、用户注册信息与账户安全管理")]),_vm._v(" "),_c('p',[_vm._v("\n                依照本协议要求，于“百居易”登记注册的会员（亦称“百居易会员”、“用户”“注册用户”），须同意以下服务条款，方有资格享受“百居易”提供的相应服务，并受本协议条款的约束。"),_c('br'),_vm._v("\n                  1．在您完成注册程序或以其他“百居易”允许的方式实际使用“百居易”服务时，您应当是具备完全民事权利能力和完全民事行为能力的自然人、法人或其他组织。若您不具备前述主体资格，则您及您的监护人应承担因此而导致的一切后果，且“百居易”有权注销（永久冻结）您的“百居易”账户，并向您及您的监护人索偿。“百居易”建议任何未满18周岁的未成年人参加网上活动应事先取得其监护人（以下简称“监护人”）的同意并遵守《青少年网络文明公约》。"),_c('br'),_vm._v("\n                  2．用户在操作注册时，应按系统提示的向“百居易”提供注册资料，并承诺所提供的注册资料真实、准确、完整、合法、有效，用户注册资料如有变动的，应及时更新。同时，用户所选用的用户名、设置的账户昵称不得侵犯他人合法权益、违反相关法律法规。否则，“百居易”有权拒绝用户注册、收回注册的账号以及暂停或终止用户使用“百居易”的全部或部分服务，因此引起的相应责任及后果由用户自行承担，用户不得将此作为取消交易、拒绝付款的理由。"),_c('br'),_vm._v("\n                  3．用户在注册成功后，应谨慎合理的保存、使用其用户名和密码，不得供第三人使用或向他人泄露，用户应对所注册的账户发生的一切行为承担相应的法律责任。用户若发现任何非法使用注册账号或存在安全漏洞的情况，可立即通知“百居易”，要求暂停相关服务。同时，用户知悉并理解“百居易”依据用户请求采取行动需要合理期限，在此之前，对已执行的指令及(或)所导致的用户损失不承担任何责任。"),_c('br'),_vm._v("\n                  4．在您按照注册页面提示填写信息、阅读并同意本协议并完成全部注册程序后或以其他“百居易”允许的方式实际使用“百居易”服务时，您即成为“百居易”会员（亦称“会员”、“用户”“注册用户”）。 使用“百居易”服务时，您必须遵守《中华人民共和国保密法》、《中华人民共和国版权法》、《计算机信息系统国际联网保密管理规定》、《中华人民共和国计算机信息系统安全保护条例》、《计算机信息网络国际联网安全保护管理办法》、《中华人民共和国计算机信息网络国际联网管理暂行规定》及其实施办法等相关法律法规的任何及所有的规定。如“百居易”有任何合理理由认为您的行为可能违反相关法律、法规、规章及条例，“百居易”有权在任何时间，不经事先通知终止向您提供服务。"),_c('br'),_vm._v("\n                  您了解并同意，“百居易”有权应政府部门（包括司法及行政部门）的要求，向其提供您在“百居易”填写的注册信息和发布记录等必要信息。\n                ")]),_vm._v(" "),_c('h4',[_vm._v("三、服务说明")]),_vm._v(" "),_c('p',[_vm._v("\n                  1．“百居易”通过互联网依法为用户提供互联网信息等服务，涉及具体产品服务的将由有资质的服务商提供。"),_c('br'),_vm._v("\n                  2．“百居易”保留随时修改或中断服务而不需通知用户的权利。百居易行使修改或中断服务的权利，不需对用户或第三方负责，但因此导致用户直接损失的除外。"),_c('br'),_vm._v("\n                  用户必须自行准备如下设备和承担如下开支："),_c('br'),_vm._v("\n                    （1）上网设备，包括但不限于电脑或者其他上网终端、调制解调器及其他必备的上网装置。"),_c('br'),_vm._v("\n                    （2）上网开支，包括但不限于网络接入费、上网设备租用费、手机流量费等。"),_c('br'),_vm._v("\n                  3．“百居易”向用户提供发布房屋出租信息，在线信息交流及第三方支付服务（本服务）。本协议中房东指通过“百居易”或其他第三方平台发布其享有所有权或使用权（含转租权）的房源信息，并对依据预订订单为房客提供房屋租赁（短期）服务的自然人或法人及其他社会团体注册用户；本协议中的房客是指通过第三方预订平台预订（承租）房东房屋的注册用户。"),_c('br'),_vm._v("\n                  4．本网站各项服务的所有权和运作权归“百居易”拥有。除非本服务条款另有其他明示规定，增加或强化目前本服务的任何新功能，包括所推出的新产品，均受到本服务协议之规范。"),_c('br'),_vm._v("\n                  5．“百居易”仅为用户提供展示其房源信息服务平台。用户了解且明白，“百居易”上所有信息均为房东或房客自由发布，用户务必自行核查发布信息的准确性、真实性以及合法性。“百居易”并不对所发布信息的准确性、真实性和合法性承担保证责任。如用户发现该等发布的信息为虚假、违法信息，请立即向“百居易”举报，“百居易”将采取删除与屏蔽，以及其他相应的技术和法律手段阻止此类信息的发布。“百居易”不对用户所发布的信息的删除或储存失败负责。"),_c('br'),_vm._v("\n                  6．因房东或房客所发布信息欠缺准确性、真实性和合法性导致您的合法权益受到损害的，“百居易”不承担任何法律责任，但可根据实际需要配合进行信息的核实与调取；“百居易”对您与房东或房客间缔结的任何合同、协议、契约等均不作为参与方或见证方，因上述合同，协议或契约的履行产生的任何争议，“百居易”不承担任何法律责任，但可根据实际需要配合进行信息的核实与调取。"),_c('br'),_vm._v("\n                  7．用户了解并同意，本服务仅依其当前所呈现的状况提供，对于任何用户通讯或个人化设定之失效、删除、传递错误、未予储存或其他任何问题，“百居易”均不承担任何责任。"),_c('br'),_vm._v("\n                  8．小格创新提供一定的解决方案保护用户第三方平台账号的稳定性，但是用户在第三方平台的账号由于各种原因被封停或注销，小格创新不承担任何责任。"),_c('br'),_vm._v("\n                  9．由于技术原因的不稳定性导致的订单遗漏、消息同步失败等带来的损失，小格创新不承担任何责任。\n                ")]),_vm._v(" "),_c('h4',[_vm._v("四、用户管理")]),_vm._v(" "),_c('p',[_vm._v("\n                  1．在“百居易”上使用“百居易”服务过程中，您承诺遵守以下约定："),_c('br'),_vm._v("\n                    （1）在使用“百居易”服务过程中实施的所有行为均遵守国家法律、法规等规范文件及“百居易”各项规则的规定和要求，不违背社会公共利益或公共道德，不损害他人的合法权益，不违反本协议及相关规则。您如果违反前述承诺，产生任何法律后果的，您应以自己的名义独立承担所有的法律责任，并确保“百居易”免于因此产生任何损失。"),_c('br'),_vm._v("\n                    （2）不发布国家禁止发布的信息（除非取得合法且足够的许可），不发布涉嫌侵犯他人知识产权或其它合法权益的信息，不发布违背社会公共利益或公共道德、公序良俗的信息，不发布其它涉嫌违法或违反本协议及各类规则的信息。"),_c('br'),_vm._v("\n                    （3）不对“百居易”网站上的任何数据作商业性利用，包括但不限于在未经“百居易”事先书面同意的情况下以复制、传播等任何方式使用“百居易”上展示的资料。"),_c('br'),_vm._v("\n                    （4）不使用任何装置、软件或例行程序干预或试图干预“百居易”的正常运作或正在“百居易”上进行的任何活动。您不得采取任何将导致不合理的庞大数据负载加诸“百居易”网络设备的行动。"),_c('br'),_vm._v(" \n                    （5）不使用网络服务作非法用途； "),_c('br'),_vm._v("\n                    （6）不干扰或混乱网络服务； "),_c('br'),_vm._v("\n                    （7）遵守所有使用网络服务的网络协议、规定、程序和惯例。"),_c('br'),_vm._v("\n                  2．您了解并同意："),_c('br'),_vm._v("\n                    （1）您违反上述承诺时，“百居易”有权依据本协议的约定，做出相应处理或终止向您提供服务，且无须征得您的同意或提前通知予您。 "),_c('br'),_vm._v("\n                    （2）根据相关法令的指定或者“百居易”服务规则的判断，您的行为涉嫌违反法律法规的规定或违反本协议和/或规则的条款的，“百居易”有权采取相应措施，包括但不限于直接屏蔽、删除侵权、降低您的信用值或单方解除本协议，停止提供相应服务。"),_c('br'),_vm._v("\n                    （3）对于您在“百居易”实施的行为，包括您未在“百居易”上实施但已经对“百居易”及“百居易”平台的其他注册用户产生影响的行为，“百居易”有权单方认定您行为的性质及是否构成对本协议和/或规则的违反，并据此采取相应的处理措施。"),_c('br'),_vm._v("\n                    （4）对于您涉嫌违反承诺的行为对任意第三方造成损害的，您均应当以自己的名义独立承担所有的法律责任，并应确保“百居易”免于承担因此产生的损失或增加的费用。"),_c('br'),_vm._v("\n                    （5）如您涉嫌违反有关法律或者本协议之规定，使“百居易”遭受任何损失，或受到任何第三方的索赔，或受到任何行政管理部门的处罚，您应当赔偿“百居易”因此造成的损失及（或）发生的费用，包括合理的鉴定费、评估费、律师费用等。\n                ")]),_vm._v(" "),_c('h4',[_vm._v("五、服务使用基本规则")]),_vm._v(" "),_c('p',[_vm._v("\n                1．您不得利用“百居易”危害国家安全、泄露国家秘密，不得侵犯国家社会集体的和公民的合法权益，不得利用“百居易”制作、复制和传播下列信息： "),_c('br'),_vm._v("\n                  （1）煽动抗拒、破坏宪法和法律、行政法规实施的；"),_c('br'),_vm._v("\n                  （2）煽动颠覆国家政权，推翻社会主义制度的；"),_c('br'),_vm._v("\n                  （3）煽动分裂国家、破坏国家统一的；"),_c('br'),_vm._v("\n                  （4）煽动民族仇恨、民族歧视，破坏民族团结的； "),_c('br'),_vm._v("\n                  （5）捏造或者歪曲事实，散布谣言，扰乱社会秩序的；"),_c('br'),_vm._v("\n                  （6）宣扬封建迷信、淫秽、色情、赌博、暴力、凶杀、恐怖、教唆犯罪的；"),_c('br'),_vm._v("\n                  （7）公然侮辱他人或者捏造事实诽谤他人的，或者进行其他恶意攻击的；"),_c('br'),_vm._v("\n                  （8）损害国家机关信誉的；"),_c('br'),_vm._v("\n                  （9）其他违反宪法和法律法规的；"),_c('br'),_vm._v("\n                  （10）未经“百居易”同意许可进行商业广告行为的。"),_c('br'),_vm._v("\n                2．在接受“百居易”服务的过程中，您不得从事下列行为："),_c('br'),_vm._v("\n                  （1）发表、传送、传播、储存侵害他人知识产权、商业秘密权等合法权利的内容。包含但不仅限于病毒、木马等可能对“百居易”系统造成伤害或影响其稳定性的内容；"),_c('br'),_vm._v("\n                  （2）传法、散布或以其他方式实现传播含有受到知识产权法律保护的图像、相片、软件或其他资料的文件；"),_c('br'),_vm._v("\n                  （3）上载、张贴、发送或传送任何非法、反动、淫秽、粗俗、猥亵的，胁迫、骚扰、中伤他人、诽谤、侵害他人隐私或诋毁他人名誉或商誉的，种族歧视、危害未成年人或其他不适当的信息或电子邮件，包括但不限于资讯、资料、文字、软件、音乐、照片、图形、张贴、信息或其他资料"),_c('br'),_vm._v("\n                  （4）上载、发送电子邮件或以其它方式传送无权传送的内容（例如内部资料、机密资料）； "),_c('br'),_vm._v("\n                  （5）使用任何包含有通过侵犯商标、版权、专利、商业机密或任何一方的其他专有权利的方式，利用本“软件”获得图像或相片的资料或信息；"),_c('br'),_vm._v("\n                  （6）进行危害计算机网络安全的行为，包括但不限于：使用未经许可的数据或进入未经许可的服务器／账号；未经允许进入公众计算机网络或者他人计算机系统并删除、修改、增加存储信息；未经许可，企图探查、扫描、测试本平台系统或网络的弱点或其它实施破坏网络安全的行为；企图干涉、破坏本平台系统或网站的正常运行，故意传播恶意程序或病毒以及其他破坏干扰正常网络信息服务的行为；"),_c('br'),_vm._v("\n                  （7）修改或伪造软件作品运行中的指令、数据、数据包，增加、删减、变动软件的功能或运行效果，不得将用于上述用途的软件通过信息网络向公众传播或者运营；"),_c('br'),_vm._v("\n                  （8）在未经“百居易”书面明确授权前提下，出售、出租、散布、转移或转授权软件和服务或相关的链接或从使用软件和服务或软件和服务的条款中获利，无论以上使用是否为直接经济或金钱收益；"),_c('br'),_vm._v("\n                  （9）违背“百居易”页面公布之活动规则，包括但不限于发布虚假信息、作弊或通过其他手段进行虚假交易；"),_c('br'),_vm._v("\n                  （10）冒充任何人或机构，或以虚伪不实的方式谎称或使人误认为与任何人或任何机构有关； "),_c('br'),_vm._v(" \n                  （11）伪造标题或以其他方式操控识别资料，使人误认为该内容为“百居易”所传送；"),_c('br'),_vm._v("\n                  （12）跟踪或以其他方式骚扰他人；"),_c('br'),_vm._v("\n                  （13）其它被“百居易”视为不适当的行为。"),_c('br'),_vm._v("\n                3．“百居易”声明用户的系统记录有可能作为用户违反法律及／或本协议的有效法律证据。“百居易”有权在下述情况下，对内容进行保存或披露： "),_c('br'),_vm._v("\n                  （1）法律程序所规定； "),_c('br'),_vm._v("\n                  （2）本服务条款规定；"),_c('br'),_vm._v("\n                  （3）被侵害的第三人提出权利主张；"),_c('br'),_vm._v("\n                  （4）为保护“百居易”、其使用者及社会公众的权利、财产或人身安全；"),_c('br'),_vm._v("\n                  （5）其他“百居易”认为有必要的情况。"),_c('br'),_vm._v("\n                4．如用户未遵守以上义务，小格创新有权作出独立判断并采取终止或中断用户使用网站服务、注销或冻结用户账户、删除或修改用户发布的信息等措施，并依法追究用户的法律责任。"),_c('br'),_vm._v("\n                5．小格创新如发现用户存在涉嫌违反本服务条款的情形，有权进行调查，要求用户对账户的异常行为进行说明并提供证据资料。在此期间，“百居易”有权暂时冻结用户账户、中止提供服务，但调查结果应在60日内告知用户。\n                ")]),_vm._v(" "),_c('h4',[_vm._v("六、知识产权保护")]),_vm._v(" "),_c('p',[_vm._v("\n                1．“百居易”网站以及在小格创新提供服务过程中的所有知识产权（包括但不限于著作权、专利、商标、服务标识、商号、设计，不论是否已登记注册）、信息内容、运营的数据库、网站设计、文字和图表、软件、照片、录像、音乐、声音及其前述组合，以及所有软件编译、相关源代码和软件（包括小应用程序和脚本）均为小格创新（或相关许可方）所有，受法律保护。"),_c('br'),_vm._v("\n                2．未经小格创新事先书面同意，上述任何材料用户均不得通过任何形式用于商业目的，包括但不限于复制、转发、拷贝、分发、再版、下载、展示、张贴或传送，亦不得以任何方式出售、出租或转租，用于制作改编作品、或以任何方式开发利用。但用户可在计算机屏幕上检索和展示网站内容，并以电子形式在硬盘上存储该等内容（但不得存储在服务器或其他与网络连接的存储设施），或为非商业的个人自用目的进行打印，但前提是用户不得更改任何著作权和专有权。"),_c('br'),_vm._v("\n                3．用户在使用服务过程中获得的任何信息的相关权利、所有权和知识产权为该信息所有人的财产，受有关著作权法或其他法律法规的保护。用户在本协议项下不对该等信息享有任何权利。"),_c('br'),_vm._v("\n                4．网站页面上的文学和艺术作品的作者已主张其为该等作品的作者，从而享有相关的权利。"),_c('br'),_vm._v("\n                5．用户同意已就用户于通过“百居易”发布、上传的内容（包括但不限于文章、评论、信息等），授予小格创新享有使用、改编以及转授权的权利，且该授权（许可）是免费、永久有效且不可撤销。"),_c('br')]),_vm._v(" "),_c('h4',[_vm._v("七、国际使用")]),_vm._v(" "),_c('p',[_vm._v("\n                1．用户知晓并了解互联网的无国界的特性，就其在“百居易”上的包括但不限于使用服务、网络以及发布评论、文章、信息等全部行为同意遵守与互联网服务相关地区（包括服务提供地、用户所在地等）所有适用的包括但不限于关于约束输出技术、数据之传输等法律法规。\n                ")]),_vm._v(" "),_c('h4',[_vm._v("八、隐私保护")]),_vm._v(" "),_c('p',[_vm._v("\n                1．小格创新承诺保护用户的个人隐私，仅以合法方式（根据《互联网信息服务管理办法》及其他有关数据保护的法规）收集和使用用户的信息。"),_c('br'),_vm._v("\n                2．小格创新收集（或保存）的用户个人标识信息，一般为用户在注册时或回复某项信息请求时自愿提供的信息。未经用户的明确同意，小格创新不收集用户的任何敏感信息。小格创新对用户信息收集（或保存）还享有以下权限。"),_c('br'),_vm._v("\n                  （1）百居易有权收集用户的互联网协议地址（“IP地址”），以通过百居易的服务器帮助诊断问题并对本网站进行管理。IP地址系指用户使用互联网时向用户的计算机分配的号码。用户的IP地址还可用于帮助在某一特定期间对用户进行识别并收集广泛的统计数据。"),_c('br'),_vm._v("\n                  （2）小格创新可要求用户提供：姓名、电子邮箱、身份证号码、电话号码和付款方式详细信息（如信用卡信息）。"),_c('br'),_vm._v("\n                  （3）如用户通过后台系统操作或其他形式的告知，更新用户个人的信息数据时，小格创新应及时更新用户的个人数据。"),_c('br'),_vm._v("\n                  （4）小格创新应将用户个人标识信息依据相关法律法规进行保护与保存，不得为任何第三方的营销目的而向其披露、出售或出租用户的个人信息。"),_c('br'),_vm._v("\n                此外，百居易始终有权为以下目的披露个人专有信息： "),_c('br'),_vm._v("\n                  a. 为了更好地向用户提供服务并对“百居易”进行广告宣传的；"),_c('br'),_vm._v("\n                  b. 用户同意让第三方共享资料的；"),_c('br'),_vm._v("\n                  c. 用户同意公开其个人资料，享受为其提供的产品和服务的； "),_c('br'),_vm._v("\n                  d. 国家法律法规或地方性规章制度所规定的；"),_c('br'),_vm._v("\n                  e. 回复政府部门或准政府部门的质询的；"),_c('br'),_vm._v("\n                  f. 依法保护“百居易”和/或本网站的其他用户的权利或财产的。"),_c('br'),_vm._v("\n                  同时，小格创新有权将用户的个人标识信息提供给“百居易”的代理商和分包商、合作商，以帮助“百居易”按照本政策的规定使用用户的数据。例如，“百居易”可能将该信息发送至信用卡服务商以完成付款程序。"),_c('br'),_vm._v("\n                3．如果小格创新发生任何出售、合并、兼并、控制权变更、重大资产转让、重组或清算，则小格创新可自行决定向有关第三方转让、出售或让与“百居易”收集的信息（包括但不限于个人专有信息和其他信息）。"),_c('br'),_vm._v("\n                4．信息安全与保存 小格创新将按照内部安全政策和相关法律规定妥善持有个人标识信息。小格创新将在该数据停止使用后的2年内保存该数据。"),_c('br'),_vm._v("\n                关于网站和用户间传送的信息，尽管小格创新将采取合理措施保护该信息，但小格创新可能无法阻止第三方未经授权获得该信息或在传送期间无意中被泄露小格创新在履行应有的保护措施基础上，无需因此承担任何责任。用户应知悉与网站联系时可能存在的风险。\n                ")]),_vm._v(" "),_c('h4',[_vm._v("九、免责条款")]),_vm._v(" "),_c('p',[_vm._v("\n                1．任何由用户、商家或其他第三方在“百居易”或基于本服务所上传、发布、发送电子邮件以及任何其他方式传送的资讯、资料、文字、软件、音乐、音讯、照片、图形、视讯、信息或其他资料（以下简称“内容”），无论是公开还是私下传送，小格创新应依据国家相关法律法规进行必要的审查，但内容提供者应对发布的信息承担法律责任。"),_c('br'),_vm._v("\n                2．小格创新对在“百居易”注册的其他用户身份以及所提供有关个人资料的真实性、准确性无能力进行准确的、完整的验证、保证其真实性、完整性，用户应在与其他用户沟通的过程中，注意保护的自身的人身及财产安全。"),_c('br'),_vm._v("\n                3．“百居易”为民宿、公寓房源信息发布与预订的平台，网站所发布的房源信息及实际房屋短租服务方由商家（房屋的所有人或经授权的使用人）提供，用户因使用（入住）该民宿、公寓（包括其他形式的不动产）遭受人身伤害、财产损害以及出现其他侵犯用户权益情况的，应当向相关责任人或商家寻求救济，但小格创新应依据国家相关法律法规的规定承担网络平台提供者的相应责任。"),_c('br'),_vm._v("\n                4．用户知晓并理解，“百居易”会不定期的出现系统维护、升级等情况，且小格创新可能对服务或功能进行升级、调整，如因上述原因暂停网络服务或原服务、功能发生变化的，小格创新将尽可能事先在网站上进行通告，且不应影响已生成订单的执行。\n                ")]),_vm._v(" "),_c('h4',[_vm._v("十、协议终止")]),_vm._v(" "),_c('p',[_vm._v("\n                1．您同意，“百居易”基于平台服务的安全性、合法性，有权不经事先通知而中止、终止向您提供部分或全部“百居易”服务，暂时冻结或永久冻结（注消）您的账户，且无须为此向您或任何第三方承担任何责任。如发生下列任何一种情形，“百居易”有权单方面中断或终止向用户提供服务而无需通知用户，如因此给其他用户和/或小格创新造成的损失，应承担赔偿责任："),_c('br'),_vm._v("\n                  （1）用户违反《中华人民共和国保守国家秘密法》、《中华人民共和国计算机信息系统安全保护条例》、《计算机软件保护条例》、信息产业部2000年10月8日第4次部务会议通过的《互联网电子公告服务管理规定》，以及《互联网新闻信息服务管理规定》等有关计算机及互联网规定的法律和法规、实施办法；"),_c('br'),_vm._v("\n                  （2）用户违反本服务协议中规定的用户管理或（和）“百居易”使用规范；"),_c('br'),_vm._v("\n                  （3）注册信息中的主要内容不真实或不准确或不及时或不完整；"),_c('br'),_vm._v("\n                  （4）未经“百居易”同意，将“百居易”平台用于商业目的；"),_c('br'),_vm._v("\n                  （5）“百居易”终止向您提供服务后，您涉嫌再一次直接或间接或以他人名义注册为“百居易”会员的；"),_c('br'),_vm._v("\n                  （6）本协议（含规则）变更时，您明示并通知“百居易”不愿接受新的服务协议的；"),_c('br'),_vm._v("\n                  （7）其它“百居易”认为应当终止服务的情况。"),_c('br'),_vm._v(" \n                2．您有权向“百居易”要求注销您的账户，经“百居易”审核同意的，“百居易”注销您的账户，届时，您与“百居易”基于本协议的合同关系即终止。您的账户被注销后，“百居易”没有义务为您保留或向您披露您账户中的任何信息，也没有义务向您或第三方转发任何您未曾阅读或发送过的信息。"),_c('br'),_vm._v("\n                3．您同意，您与“百居易”的协议关系终止后，“百居易”仍享有下列权利：