
import Header from '@/components/header/Header.component'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { mapState, mapMutations, mapActions } from 'vuex'
import { Http } from '@/service/http.service'
import { Message } from '@/store/message'
import * as moment from 'moment'
export default {
name: 'MessageDetail',
components: {
Header,
XTextarea,
Group,
XButton,
Loading
},
data () {
return {
loading: false,
thread_id: '',
message: ''
}
},
async created () {
const query = this.$route.query
this.thread_id = query.thread_id
this.loading = true
await this.getMessages({ thread_id: this.thread_id })
this.loading = false
this.scrollToBottom()
},
beforeRouteLeave (to, from, next) {
this.clearMessages()
this.clearCustomer()
next()
},
computed: {
...mapState({
messages: state => state.message.messages,
customer: state => state.message.customer,
house_id: state => state.message.house_id
})
},
methods: {
...mapMutations('message', [
'clearMessages',
'clearCustomer',
'appendMessage',

]),
...mapActions({
getMessages: 'message/getMessages',
sendMessage: 'message/sendMessage'
}),
send () {
if (!this.message) {
return
}
const inputMessage = this.message
const message = new Message({
origin_create_time: moment(),
thirdparty_customer_type: 1,
message: '发送中...'
})
this.appendMessage(message)
this.message = ''
this.scrollToBottom()

this.sendMessage({
thread_id: this.thread_id,
message: inputMessage
}).then(res => {
message.message = inputMessage
}).catch(err => {
message._old = inputMessage
message.message = '发送失败'
message.send_status = false
})
},
retry (message) {
message.message = '发送中...'
message.send_status = true
this.sendMessage({
thread_id: this.thread_id,
message: message._old
}).then(res => {
message.message = message._old
}).catch(err => {
message.message = '发送失败'
message.send_status = false
})
},
btnAction (action) {
if (!action) {
return
}
if (action.type === 'redirect') {

if (action.url.startsWith('http')) { /** 第三方url */

let url = action.url.includes('?') ? action.url + '&browser_have_title=1' : action.url + '?browser_have_title=1'
triggerAClickEvent(url)
}
} else if (action.type === 'post') {
Http.post(
action.url,
action.data
)
}
/** 模拟a标签的点击事件 */
function triggerAClickEvent (url) {
let el = document.createElement("a");
el.href = url;
el.target = '_blank';
el.click();
}
},
scrollToBottom () {
this.$nextTick(() => {
const container = this.$refs['container']
container.scrollTop = container.scrollHeight
})
}
}
}



