import axios from 'axios'
import router from '@/router'
import Vue from 'vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import { ERROR_CODE } from '@/config'

Vue.use(ToastPlugin)

function fetch (method, url, params) {
return new Promise((resolve, reject) => {
axios[method](url, params)
.then(response => {
resolve(response.data)
})
.catch(error => {
reject(error)
})
})
}

axios.interceptors.response.use(response => {
if ([ ERROR_CODE.USER_AUTH_FAILED, ERROR_CODE.USER_NEED_FINISH_INFO ].includes(response.data.error_code)) {
router.push({ path: '/login' })
}
else if (response.data.error_code !== ERROR_CODE.SUCCESS) {
Vue.$vux.toast.show({
time: 3000,
type: 'warn',
text: response.data.error_msg
})
}
return response
})

export const Http = {
get (url, params) {
return fetch('get', url, { params: params })
},

post (url, params) {
return fetch('post', url, params)
}
}



