
import Header from '@/components/header/Header.component'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import DatetimeView from 'vux/src/components/datetime-view/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
import * as moment from 'moment'
import { THIRDPARTY, STATUS } from '@/config'

const status = {
'all': { value: 'all', name: '全部订单' },
...STATUS
}
const platform = {
4: { value: 4, name: '全部平台' },
...THIRDPARTY
}
export default {
name: 'OrderList',
components: {
Header,
Group,
Datetime,
DatetimeView,
InlineLoading,
Loading
},
data() {
return {
statusMap: status,
platformMap: platform,
filter: {
status: 'all',
platform: 4,
begin: moment().subtract(1, 'M').format('YYYY-MM-DD'),
end: moment().format('YYYY-MM-DD'),
selecting: false,
selectType: 0, // 0-状态 | 1-平台 | 2-日期
statusOptions: Object.values(status),
platformOptions: Object.values(platform),
dateOptions: {
min: moment().subtract(1, 'year').format('YYYY-MM-DD'),
max: moment().add(3, 'year').format('YYYY-MM-DD')
}
},
loading: false,
pageLoading: false,
page: 1,
pageSize: 15
}
},
watch: {
'filter.selecting': function (val) {
if (!val) {
this.filterOrders()
}
}
},
computed: {
...mapState({

total: state => state.order.total
}),
...mapGetters('order', [
'orders'
])
},
async created () {
this.getOrders(this.page)
},
methods: {
...mapMutations('order', [
'setOrderByIndex'
]),
...mapActions({
initOrders: 'order/initOrders',
declineOrder: 'order/declineOrder',
approveOrder: 'order/approveOrder'
}),
filterOrders () {
this.page = 1;
const params = {
page: this.page,
page_size: this.pageSize,
begin: this.filter.begin,
end: this.filter.end
}
if (this.filter.status !== 'all') {
Object.assign(params, { status: this.filter.status })
}
if (this.filter.platform !== 4) {
Object.assign(params, { thirdparty_type: this.filter.platform })
}
this.initOrders(params)
},
async loadMore () {
this.loading = true
await this.getOrders(++this.page)
this.loading = false
},
async getOrders (page) {
this.pageLoading = true
await this.initOrders({
page: page,
page_size: this.pageSize,
begin: this.filter.begin,
end: this.filter.end
})
this.pageLoading = false
},
async decline (order, index) {
let res = await this.declineOrder({ reservation_code: order.reservation_code })
if (res && res.error_code === 0) {
const o = Object.assign({}, order, { status: 'denied' })
this.setOrderByIndex({
order: o,
index: index
})
}
},
async approve (order, index) {
let res = await this.approveOrder({ reservation_code: order.reservation_code })
if (res && res.error_code === 0) {

const o = Object.assign({}, order, { status: 'accepted' })
this.setOrderByIndex({
order: o,
index: index
})
}
}
}
}



