
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import { Store } from '@/service/store.service'
import { Http } from '@/service/http.service'
export default {
name: 'modify',
data() {
return {
verifyCodeBtnText: '获取验证码',
err: false,
account: '', // 手机验证码,
threeNum: 0,
threeErr: false, // false true 三次验证
image_code: '',
imgCodeSrc: '/mobile_api/user/image_code?nocache=1',
goods: [],
verifyCodeBtnDisabled: false,
timeout: 0, //倒计时
code: '',
action_type: 'reset_password',
backendErrors: {},
misOrSet: 1,
new_password: '',
password_confirm: '',
description: 'msg description',
icon: '',
verifyCodeBtnDisabled:false,
buttons: [{
type: 'primary',
text: '返回登录',
link: '/login',

}]
}
},
components: {
XInput,
XButton,
Msg,
Divider,
},
watch: {
threeNum: {
handler () {
if (this.threeNum === 200001) {
this.threeErr = true;
}
}
},
},
methods: {


backFn () {
this.$router.go(-1)
},

refImgFn () {
console.log(123)
this.imgCodeSrc = this.imgCodeSrc + Date.now()
},


commitFn () {
const data = {'code': this.code, 'new_password': this.new_password, 'password_confirm': this.password_confirm}
const MODIFY_PASSWORD_API = '/mobile_api/user/modify_password'

Http.post(MODIFY_PASSWORD_API, data).then((res) => {
console.log(res)
if(res.error_code === 0) {
this.misOrSet = 3
}
},(err) => {console.log(err)})
},


checkCodeFn() {
const a = window.document.cookie.split(';')
console.log(a)
a.map(item => {
if(item.indexOf('user=')!==-1) {
this.account = item.slice(6)
}
})
const data = {'code':this.code, 'account': this.account, 'action_type': this.action_type}
const CHECK_CODE_API = '/mobile_api/user/check_code'
Http.post(CHECK_CODE_API, data).then((res) => {
console.log(res)
if(res.error_code === 0) {
this.misOrSet = 2
}
if(res.error_msg) {
this.err = true
this.backendErrors = res.error_msg
}
}, (err) => {console.log(err)})
},

getCodeFn () {
const a = window.document.cookie.split(';')
console.log(a)
a.map(item => {
if(item.indexOf('user=')!==-1) {
this.account = item.slice(6)
}
})
if (this.account.indexOf('@') !=-1) {
console.log('youxiang')
this.getEmailCode()
} else  {
console.log('shouji2')
this.getVerifyCode()
}
},

/**
* 获取手机验证码
* @param mobile string
* @param type 1 - 注册 2 - 找回密码
*/
getVerifyCode () {
if(!this.verifyCodeBtnDisabled) {
this.verifyCodeBtnDisabled =true
this.clearBackendErrors()
this.err = false
const CODE_API = '/mobile_api/user/code'
const data = {'account': this.account, 'action_type': 'reset_password', 'image_code': this.image_code}
Http.get(CODE_API, data).then((res) => {
if (200001 === res.error_code) {
this.threeNum = res.error_code;
}
console.log(res)
if (res.data && res.error_msg) {
this.backendErrors = res.error_msg
}
this.subscribeBtnText() 
}, (err) => console.log(err))

}
},
/**
* 获取邮箱验证码
* @param mobile string
* @param type 1 - 注册 2 - 找回密码
*/
getEmailCode () {
this.inputEmYanErr = false
this.clearBackendErrors()
this.err = false
if(!this.verifyCodeBtnDisabled) {
this.verifyCodeBtnDisabled = true;
const CODE_API = '/mobile_api/user/code'
const data = {'account': this.account, 'action_type': 'reset_password'}
Http.get(CODE_API, data).then((res) => {
if (200001 === res.error_code) {
this.threeNum = res.error_code;
}
console.log(2)
console.log(res)
this.subscribeBtnText() 
}, (err) => {
console.log(err)})

}
},

subscribeBtnText () {
this.verifyCodeBtnDisabled = true
const jishi = setInterval(() => {



this.$nextTick(() => {
if (this.timeout === 0) {
this.verifyCodeBtnDisabled = false;
this.verifyCodeBtnText = '获取验证码';
this.timeout = 3;
clearInterval(jishi)
}else {
this.verifyCodeBtnText = '已发送' + (this.timeout--) + 's';
}
})
}, 1000)
},

clearBackendErrors () {
this.backendErrors = {}
}
}
}



