
import Badge from 'vux/src/components/badge/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
export default {
name: 'MessageList',
components: {
Badge,
Loading
},
data () {
return {
loading: false
}
},
async created () {
this.loading = true
await this.getThreads()
this.loading = false
},
computed: {
...mapState({
threads: state => state.message.threads
}),



},
methods: {
...mapActions({
getThreads: 'message/getThreads',
}),
toMessageDetail (thread) {
this.$router.push({ path: '/message/detail', query: { thread_id: thread.id } })
}
}
}



